.post,
article.page,
.single .post,
.search .post.format-gallery,
.search .post.format-image,
.search .post.format-video,
.search .post.format-audio {
	background-color: var(--white);
	border-radius: 2px;
	box-shadow: $box-shadow-small;
}

.post:hover,
article.page:hover,
.single .post:hover,
.search .post.format-gallery:hover,
.search .post.format-image:hover,
.search .post.format-video:hover,
.search .post.format-audio:hover {
	box-shadow: $box-shadow-large;
}

.entry-content,
.entry-summary {
	padding: 1.5em 0;
}

@media screen and (min-width: 80em) {
	.jgd-column-1 .entry-title,
	.three-fourths-centered-r .entry-title,
	.jgd-column-1 .entry-meta,
	.three-fourths-centered-r .entry-meta,
	.jgd-column-1 .entry-footer,
	.three-fourths-centered-r .entry-footer {
		padding: 0 12.5%;
	}

	.format-aside.three-fourths-centered-r .entry-meta,
	.format-link.three-fourths-centered-r .entry-meta,
	.format-quote.three-fourths-centered-r .entry-meta,
	.format-status.three-fourths-centered-r .entry-meta {
		padding: 0;
	}
}

.page-header {
	width: 100%;
}

.entry-title,
.page-title,
.widget-title,
.widgettitle,
.comments-title,
.entry-title-imagegrid {
	@include font-size(1.5);
	font-weight: $font__weight-bold;
	line-height: 2rem;

	a:link {
		color: var(--primary-color);
	}

	a:visited {
		color: var(--primary-color-medium);
	}

	a:hover,
	a:focus {
		color: var(--primary-color-dark);
	}
}

.page-title {
	margin-left: 1rem;
	margin-right: 1rem;
}

@media (min-width: 48em) {
	.page-title {
		margin-left: 12.5%;
		margin-right: 12.5%;
	}

	.two-by-two .page-title {
		margin-left: 0;
		margin-right: 0;
	}
}

@media (min-width: 80em) {
	.centered .page-title {
		margin-left: 25%;
		margin-right: 25%;
	}
}

.single .entry-title,
.page .entry-title {
	@include font-size(2.125);
}

.blog .entry-title,
.archive .entry-title,
.search .entry-title {
	margin-top: 1rem;
}

.template-entry-title {
	width: 100%;
}

.search .entry-title {
	@include font-size(1.5);
}

.search .cat-links,
.search .tags-links,
.search .posted-on,
.search .format-image.has-post-thumbnail .posted-on {
	margin-bottom: 0;

	a:link,
	a:visited {
		@include restore-link-style;
	}

	a:hover,
	a:focus {
		@include restore-link-hover-style;
	}

}

.search .featured-image {
	background-color: transparent;
}

.search .jgd-column-1 .featured-image,
.search .two-by-two-centered-r .featured-image {
	margin: 2em -1em 1em;
}

@media screen and (min-width: 37.5em) {
	.search .featured-image + .entry-summary p {
		margin: 0 1em;
	}
}

.search .no-results {
	width: 100%;
}

.search .no-results .page-content {
	margin-bottom: 1.5em;
}

.landing-to-parent {
	padding: 0 1em;
	text-align: center;
}

.landing-to-parent a:link,
.landing-to-parent a:visited {
	color: var(--primary-text-color-white);
	font-weight: $font__weight-bold;
}

.landing-to-parent a:hover,
.landing-to-parent a:focus {
	color: var(--white);
}

.page-template-template-landing {
	article.page {
		@include no-card-style;
	}
}

@media screen and (min-width: 37.5em) {
	.landing-to-parent {
		text-align: left;
	}

	.search .featured-image,
	.search .has-post-thumbnail .scrim {
		width: calc(100% + 1em);
	}

	.search img.wp-post-image {
		margin: 0;
	}

	.search .featured-image,
	.search .three-fourths-centered-r .featured-image,
	.search .jgd-column-1 .featured-image,
	.search .two-by-two-centered-r .featured-image,
	.search .featured-image + .entry-summary {
		max-height: 300px;

		img {
			max-height: 300px;
		}
	}

	.search .entry-summary {
		padding: 0 1em;
	}
}

@media screen and (min-width: 48em) {
	.search .featured-image,
	.search .has-post-thumbnail .scrim {
		width: calc(100% + 1.5em) !important;
	}

	.search .three-fourths-centered-r .featured-image,
	.search .jgd-column-1 .featured-image,
	.search .two-by-two-centered-r .featured-image {
		margin: 2em -1em 1em -1.5em;
	}
}

@media (min-width: 80em) {
	.search .jgd-column-1 .featured-image,
	.search .two-by-two-centered-r .featured-image {
		margin: 2em 0 1em -1.5em;
	}
}

.more-link {
	@include link-button-style;
	display: inline-block;
	margin: 0 0.5em 0.5em 0.5em;
	padding: 0.65em 1em;
	vertical-align: top;
}

@media screen and (min-width: 80em) {
	.three-fourths-centered-r .entry-content .more-link {
		margin-left: 12.5%;
	}

	.jgd-column-1 .entry-content .more-link {
		margin-left: 25%;
	}

	.three-fourths-centered-r .entry-content p > .more-link,
	.jgd-column-1 .entry-content p > .more-link {
		margin: 1em 0 0 0;
	}
}

.more-images-link {
	a {
		@include link-button-style;
		padding: 0.65em 1em;
		display: block;
		text-align: center;
		width: 100%;
	}
}

.cat-links,
.tags-links,
.edit-link,
.comments-link,
.posted-on {
	margin-bottom: 0.5rem;
}

.cat-links .material-icons,
.tags-links .material-icons,
.edit-link .material-icons,
.comments-link .material-icons,
.byline .material-icons,
.posted-on .material-icons,
.image-size-meta .material-icons,
.entry-title .material-icons,
.single .format-image.has-post-thumbnail .entry-title .material-icons {
	color: var(--primary-text-color-faded-black);
	margin-right: 0.5em;
	vertical-align: bottom;
}

.cat-links a,
.tags-links a,
.edit-link a,
.comments-link a,
.posted-on a {

	&:link,
	&:visited {
		@include link-highlight-style;
		white-space: nowrap;
	}

	&:hover,
	&:focus {
		@include link-highlight-style-hover;
	}

}

.cat-links a,
.tags-links a,
.edit-link a,
.comments-link a {
	line-height: 2rem;
}

.sticky {
	background-color: var(--primary-color);
	color: var(--white);
	display: block;

	a:link,
	a:visited,
	.entry-title a:link,
	.entry-title a:visited {
		color: var(--primary-text-color-white);
	}

	a:hover,
	a:focus,
	.entry-title a:hover,
	.entry-title a:focus {
		color: var(--white);
	}

	a.more-link:link,
	a.more-link:visited {
		color: var(--primary-text-color);
	}

	a.more-link:hover,
	a.more-link:focus {
		color: var(--black);
	}

	.material-icons {
		color: var(--white);
	}

}

.byline,
.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-links {
	clear: both;
	font-weight: $font__weight-bold;
	margin: 0 0 1.5em 0;
	text-align: center;

	a {
		border-radius: 2px;
		padding-top: 0.875em;
	}

	a:hover,
	a:focus {
		background-color: var(--black-opacity-2);
	}

	.post-page-numbers {
		display: inline-block;
		min-height: 3rem;
		min-width: 3rem;
		text-align: center;
	}
}

/* Gutenberg Blocks */

.wp-block-button.is-style-outline {
	.wp-block-button__link {
		background-color: transparent;
		box-shadow: none;
		border: 1px solid var(--silver);
		color: var(--wp--preset--color--primary-brown);

		&:hover {
			background-color: var(--primary-color-opacity-04);
		}

		&:active,
		&:focus {
			background-color: var(--primary-color-opacity-12);
		}
	}
}

.wp-block-button.is-style-fill.material-ripple .wp-block-button__link {
	margin-bottom: 3px; /* Adds margin to the bottom of filled button to counter overflow:hidden in .material-ripple class */
}

.wp-block-button {
	&__link {
		background-color: var(--wp--preset--color--accent-amber);
		height: auto;
		padding: 1em !important;

		&:hover,
		&:focus {
			color: var(--wp--preset--color--primary-text-color);
		}
	}
}

.wp-block-buttons {
	display: flex;
	justify-content: center;
	margin-top: 1rem;
	margin-bottom: 1rem;

	.wp-block-button.wp-block-button {
		margin-right: 0;
		margin-bottom: 0;
	}
}

.is-style-the-mx-columns {
	padding: 0;
}

.is-style-the-mx-columns,
.site-header-blocks > .wp-block-cover {
	margin-top: 0;
	margin-bottom: 0;
}

.wp-block-column {
	.wp-block-buttons {
		flex-direction: column;
	}

	.wp-block-button {
		align-self: center;
	}
}

.wp-block-file {
	padding: 1em;
	text-align: center;

	a.wp-block-file__button {
		background-color: var(--wp--preset--color--accent-amber);
		margin: 0 1em;
		padding: 1em;
		
		&:focus,
		&:hover {
			background-color: var(--wp--preset--color--accent-dark-amber);
		}

		&:active,
		&:focus,
		&:hover,
		&:visited {
			box-shadow: $box-shadow-small;
			color: var(--wp--preset--color--primary-text-color);
			opacity: 1;
		}
	}
}

.is-style-the-mx-group-p1em {
	padding: 1em;
}

.wp-block-cover-image.alignwide {
	max-width: calc(100% - 2em);
}

.wp-block-media-text,
.wp-block-media-text.has-media-on-the-right {
	grid-template-areas:
		"media-text-media media-text-media"
		"media-text-content media-text-content";
}

/* .wp-block-navigation */
.wp-block-navigation {
	padding-top: 0.5rem;
}

.wp-block-navigation__responsive-container-open,
.wp-block-navigation__responsive-container-close {
	box-shadow: none;
	min-height: 3rem;
	min-width: 3rem;
	padding: 0.75rem;

	&:focus,
	&:hover {
		background-color: var(--black-opacity-2);
	}
}

.wp-block-navigation__responsive-container-open {
	color: var(--wp--preset--color--primary-text-color);
}

.wp-block-navigation__responsive-container-close {
	color: var(--wp--preset--color--primary-text-color);
}

.wp-block-navigation:not(.has-background) .wp-block-navigation__responsive-container.is-menu-open {
	color: var(-wp--preset--color--primary-brown);

	a:focus,
	a:hover {
		color: var(--wp--preset--color--primary-dark-brown);
	}
}

/* Custom style overrides */
.is-style-the-mx-nav {
	.is-menu-open {
		background-color: var(--wp--preset--color--white) !important;
		color: var(--wp--preset--color--primary-brown) !important;
		
		.wp-block-navigation-item__content:focus,
		.wp-block-navigation-item__content:hover {
			color: var(--wp--preset--color--primary-dark-brown);
		}
	}
}

.is-style-the-mx-nav.items-justified-left,
.is-style-the-mx-nav.items-justified-right {
	.wp-block-navigation__responsive-container-open {
		margin: 0 auto;
	}
}

.is-style-the-mx-nav.the-mx-nav-lightbg .is-menu-open .wp-block-navigation-item__content {
	color: var(--wp--preset--color--primary-text-color-medium-black);
	
	&:focus,
	&:hover {
		color: var(--wp--preset--color--primary-text-color);
	}
}

.the-mx-nav-darkbg .wp-block-navigation__responsive-container-open {
	color: var(--wp--preset--color--primary-text-color-white);
}

@media screen and (min-width: 37.5em) {
	.wp-block-navigation {
		padding-top: 0;
	}
	
	.wp-block-navigation,
	.wp-block-navigation .wp-block-page-list,
	.wp-block-navigation__container,
	.wp-block-navigation__responsive-container-content {
		gap: 0;
	}

	.wp-block-navigation .wp-block-navigation-item__content {
		border-bottom: 2px solid transparent;
		height: 3rem;
		padding: 0.75em 1em;
		
		&:hover,
		&:focus {
			border-bottom-color: var(--wp--preset--color--accent-amber);
		}
	}
	
	.wp-block-navigation .current-menu-item .wp-block-navigation-item__content {
		border-bottom-color: var(--wp--preset--color--accent-amber);
	}
	
	.wp-block-navigation.has-background .has-child .wp-block-navigation__submenu-container .wp-block-navigation__submenu-container {
		top: 100%;
		left: 25%;
	}

	.wp-block-navigation .wp-block-navigation__submenu-icon {
		box-shadow: none;
	}

	.wp-block-navigation .has-child :where(.wp-block-navigation__submenu-container) {
		box-shadow: $box-shadow-medium;
	}
	
	.wp-block-navigation__submenu-container .wp-block-navigation__submenu-icon {
		color: var(--black);
	}
	
	.wp-block-navigation .wp-block-navigation__submenu-container {
		.wp-block-navigation-item__content {
			border-bottom-color: transparent;
			height: auto;
		} 
	}
	
	/* Custom style overrides */
	.is-style-the-mx-nav .wp-block-navigation-item__content {
		text-transform: uppercase;
	}
	
	.is-style-the-mx-nav .open-on-click {
		.wp-block-navigation-item__content {
			box-shadow: none;
			
			&:active,
			&:focus,
			&:hover {
				background-color: transparent;
			}
		}
		
		.wp-block-navigation__submenu-icon {
			height: auto;
		}
	}
	
	
	
	.is-style-the-mx-nav .wp-block-navigation__submenu-container .wp-block-navigation-item__content {
		text-transform: none;
	}
	
	.is-style-the-mx-nav .wp-block-navigation__submenu-icon {
	 	background-color: transparent;
	 	height: 3rem;
		padding: 0 1em;
		width: 3rem;
	}
	 
	.is-style-the-mx-nav .wp-block-navigation__submenu-icon svg {
	 	width: 0.875rem;
	}
	 
	.is-style-the-mx-nav .wp-block-navigation__submenu-container {
	 	.wp-block-navigation-item__content {
			color: var(--wp--preset--color--primary-text-color) !important;
			
			&:focus,
			&:hover {
				color: var(--wp--preset--color--accent-darker-amber) !important;
			}
		}
	 	
	 	.wp-block-navigation-item {
	 		background-color: var(--white) !important;
	 	}
	}
	
	.is-style-the-mx-nav .has-child:where(:not(.open-on-click)):hover >.wp-block-navigation__submenu-container {
		visibility: hidden;
		overflow: hidden;
		opacity: 0;
	}
	
	.wp-block-cover .is-style-the-mx-nav:last-child {
		margin-bottom: -1em;
	}
}

.wp-block-quote.is-style-large {
	font-weight: $font__weight-normal;
	font-size: 1.5rem;
	line-height: 1.25rem;
}

.wp-block-quote.is-style-large p {
	font-style: normal;
	line-height: normal;
}

.wp-block-quote.is-style-large p,
.wp-block-quote.is-style-large cite {
	color: rgba(0, 0, 0, 0.54);
}

.wp-block-quote.is-style-large cite {
	font-style: italic;
	font-size: 1rem;
	font-weight: $font__weight-bold;
}

.wp-block-pullquote {
	border-top: transparent;
	border-bottom: transparent;
	max-width: 100%;
}

.wp-block-pullquote blockquote {
	border-left: transparent;
}

.wp-block-pullquote.aligncenter {
	margin-left: 25%;
	margin-right: 25%;
}

.wp-block-pullquote.alignleft,
.wp-block-pullquote.alignright {
	width: 50%;
}

.wp-block-pullquote.is-style-solid-color,
.wp-block-pullquote.is-style-solid-color blockquote {
	padding-left: 0;
}

.wp-block-pullquote cite,
.wp-block-pullquote.is-style-solid-color blockquote cite,
.wp-block-quote cite, {
	font-style: italic;
}

.wp-block-pullquote > p {
	font-weight: $font__weight-bold;
}

.site-header-blocks .wp-block-search {
	padding: 0.5rem 1rem;
}

input.wp-block-search__input {
	background-color: transparent;
	border-bottom-color: var(--wp--preset--color--accent-amber);
	
	.is-style-the-mx-search-darkbg & {
		color: var(--wp--preset--color--white);
	}
}

input.wp-block-search__input::placeholder {
	font-weight: $font__weight-heavy;
	opacity: 1;
	
	.is-style-the-mx-search-darkbg & {
		color: var(--wp--preset--color--primary-text-color-light-white);
	}
}

.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper {
	border-top: transparent;
	border-left: transparent;
	border-right: transparent;
	border-bottom: 2px solid var(--wp--preset--color--accent-amber);
	padding: 0;
}

.wp-block-search__button {
	background-color: transparent;
	border: transparent;
	box-shadow: none;
	min-height: 3rem;
	min-width: 3rem;
	padding: 0.75rem;
	
	&:focus,
	&:hover {
		background-color: var(--black-opacity-2);
		/*color: var(--white);*/
	}
	
	.the-mx-search--light-bg & {
		color: var(--wp--preset--color--primary-text-color) !important;
	}
}

.wp-block-site-title {
	font-size: 2.125rem; /* ~34px */
	line-height: 2.5rem;
}

.wp-block-site-title a {
	color: inherit !important;
	font-weight: 400;
}

.wp-block-site-title,
.wp-block-site-tagline {
	margin: 0;
}

.wp-block-social-links {
	padding: 0.5rem;
}

.is-style-the-mx-social-badges {
	gap: 0 !important;
	
	&.alignleft,
	&.alignright {
		justify-content: center;
		width: 100%;
	}
	
	.wp-social-link {
		background-color: transparent !important;
		
		a {
			padding: 0.5rem;
		}
	}
}

@media (min-width: 37.5em) {
	.is-style-the-mx-social-badges {
		&.alignleft {
			justify-content: flex-start;
			width: auto;
		}
		
		&.alignright {
			justify-content: flex-end;
			width: auto;
		}
	}
}

ul.wp-block-social-links {
	margin-bottom: 0;
}

.wp-block-table {
	table {
		margin-bottom: 0;
	}

	strong {
		color: rgba(0, 0, 0, 0.6);
	}
}

.wp-block-table.is-style-stripes {
	border-bottom-color: var(--primary-color-pale);

	td,
	th {
		border: none;
	}

	tbody tr:hover {
		background-color: transparent;
	}

	tbody tr:nth-child(odd) {
		background-color: var(--primary-color-pale);
	}
}

@media screen and (min-width: 37.5em) {
	.wp-block-media-text {
		grid-template-areas:
			"media-text-media media-text-content";
	}

	.wp-block-media-text.has-media-on-the-right {
		grid-template-areas:
			"media-text-content media-text-media"
	}
}

.wp-block-code code {
		font-family: var(--pre-font);
}

.wp-block-verse {
	background-color: transparent;
	font-family: var(--body-font);
}

.wp-block-image.alignfull {
	background-color: var(--wp--preset--color--primary-darker-brown);
}

.wp-block-image.alignfull img,
.wp-block-image.alignwide img {
	@include center-block;
}

@media (min-width: 80em) {
	.wp-block-separator.is-style-wide {
		margin: 1.5em 4em !important;
		max-width: calc(100% + 4em) !important;
	}
}

/* Widget Blocks */
.wp-block-archives,
.wp-block-categories,
.wp-block-latest-comments,
.wp-block-latest-posts {
	@include font-size(0.875);
	padding: 0 1rem;

	li {
		@include vertical-menu;
	}

	a:link,
	a:visited {
		color: var(--wp--preset--color--primary-text-color);
	}

	a:hover,
	a:focus {
		color: var(--wp--preset--color--accent-darker-amber);
	}
}

.wp-block-categories {
	li > ul.children {
		padding-top: 1em;
	}
}

.wp-block-latest-comments__comment-date {
	color: var(--wp--preset--color--primary-text-color-faded-black);
	@include font-size(0.875);
}

.wp-block-calendar {
	margin-bottom: 1em;

	table {
		margin-bottom: 0;
	}

	table caption {
		background-color: var(--wp--preset--color--primary-brown);
		color: var(--wp--preset--color--white);
		@include font-size(2);
	}

	table tbody {
		color: var(--wp--preset--color--primary-text-color);
	}

	td a {
		background-color: var(--wp--preset--color--primary-brown);
		color: var(--wp--preset--color--white);
		border-radius: 50%;
		display: inline-table;
		height: 1.5em;
		width: 1.5em;
	}

	table th {
		background-color: var(--primary-color-pale);
	}

	th,
	tbody td {
		border: transparent;
	}

	tbody tr:hover {
		background-color: transparent;
	}

	.wp-calendar-nav {
		display: flex;
		justify-content: space-between;
		padding: 0.5em;
	}
}

/* Attachment pages */
.attachment {
	background-color: var(--white);
}

.entry-attachment-image img {
	@include center-block;
}

article.attachment {
	margin: 0;
	padding: 0 0 2em 0;
	width: 100%;
}

.entry-attachment-image {
	p {
		margin-top: 0;
	}

	.wp-caption-text {
		padding-bottom: 1rem;
	}
}

.return-to-parent {
	background-color: var(--primary-color-light);
	padding: 1rem;
	text-align: center;
	width: 100%;

	a:link,
	a:visited {
		color: var(--white);
	}

}

.return-to-text {
	color: var(--primary-text-color-white);
	font-weight: $font__weight-heavy;
}

@media (min-width: 37.5em) {
	.attachment .entry-header,
	.attachment .entry-content > :not(.entry-attachment-image),
	.attachment .entry-footer,
	.entry-attachment-image .wp-caption-text {
		@include center-75-percent;
	}

	.entry-attachment-image img {
		padding-top: 3em;
	}
}

/* Fixes for Colorbox popups; only use with attachment.* class prepended!
.colorbox class only displays when Colorbox is enabled in the Customizer- get_theme_mod('the_mx_enable_colorbox') */
body.attachment.colorbox .entry-content {
	padding-top: 0;
}

body.attachment.colorbox .entry-attachment-image {
	margin: 0 0.5em;
}

body.attachment.colorbox .entry-attachment-image p {
	background-color: var(--primary-color-pale);
	margin-left: -15.5%;
	margin-right: -15.5%;
}

body.attachment.custom-background.colorbox {
	background-image: none;
}

body.attachment.colorbox img {
	padding-bottom: 0;
	padding-top: 0;
}

@media screen and (min-width: 37.5em) {
	body.attachment.colorbox .entry-attachment-image {
		margin-left: -1em;
		margin-right: -1em;
		width: calc(100% + 2em);
	}
}

@media screen and (min-width: 80em) {
	body.attachment.colorbox .entry-attachment-image {
		margin-left: -1.5em;
		margin-right: -1.5em;
		width: calc(100% + 3em);
	}
}
